import instance from './request';

export async function getBlogList(page = 1, limit = 10, categoryid = -1) {
  const res = await instance.get('/api/blog', {
    params: {
      page,
      limit,
      categoryid,
    },
  });
  return res;
}
export async function getBlogCategory() {
  const res = await instance.get('/api/blogtype');
  return res;
}
export async function getBlog(id) {
  const res = await instance.get(`/api/blog/${id}`);
  return res;
}
export async function postComment(options) {
  const res = await instance.post('/api/comment', options);
  return res;
}

export async function getComment(options) {
  const res = await instance.get('/api/comment', options);
  return res;
}
