import Icon from '@/components/Icon/index.vue';
import getComponentRootDom from './getComponentRootDom';
import Style from './message.module.less';

function messageBox(option = {}) {
  const content = option.content || '';
  const type = option.type || 'info';
  const duration = option.duration || 2000;
  const container = option.container || document.body;
  const { calllback } = option;
  if (option.container) {
    if (getComputedStyle(container).position === 'static') {
      container.style.position = 'relative';
    }
  }

  const div = document.createElement('div');
  const icon = getComponentRootDom(Icon, { type });
  const typeClassName = Style[`message-${type}`];
  div.className = `${Style.message} ${typeClassName}`;
  div.innerHTML = `<span class=${Style.icon}>${icon.outerHTML}</span> <span>${content}</span>`;
  container.appendChild(div);
  console.log(div.clientHeight);
  div.style.opacity = 1;
  div.style.transform = 'translate(-50%,-50%)';
  setTimeout(() => {
    div.style.opacity = 1;
    div.style.transform = 'translate(-50%,-50%) translateY(-20px)';
    div.addEventListener('transitionend', () => {
      div.remove();
      if (calllback) {
        calllback();
      }
    }, { once: true });
  }, duration);
}
export default messageBox;
