<template>
  <div class="to-top-container" @click="handle" v-show="isShow">
      Top
  </div>
</template>

<script>
/* eslint-disable no-param-reassign */
import deBounce from '@/utils/debounce';

export default {
  data() {
    return {
      isShow: false,
    };
  },
  created() {
    this.$bus.$on('mainscroll', deBounce(this.handleTop, 100));
  },
  destroyed() {
    this.$bus.$off('mainscroll', deBounce(this.handleTop, 100));
  },
  methods: {
    handleTop(dom) {
      if (!dom) {
        this.isShow = false;
        return;
      }
      if (dom.scrollTop >= 3000) {
        this.isShow = true;
      } else {
        this.isShow = false;
      }
    },
    handle() {
      this.$bus.$emit('changescroll', 0);
    },
  },
};
</script>

<style lang="less" scoped>
.to-top-container{
    width: 70px;
    height: 70px;
    text-align: center;
    line-height: 70px;
    border-radius: 50%;
    background-color: lightskyblue;
    position: fixed;
    bottom: 20px;
    right: 300px;
    color: #fff;
    cursor: pointer;
    opacity: .5;
    &:hover{
        background-color: blue;
        opacity: 1;
    }
    }
</style>
