<template>
  <div class="pager-container"
  v-if="total>vessionNum"
  >
    <a :class="{disable:current===1}"
    @click="handleClick(1)"
    >|&lt;&lt;</a>
    <a
    @click="handleClick(current -1)"
    :class="{disable:current===1}"
    >&lt;&lt;</a>
    <a :class="{active:current===item}"
    @click="handleClick(item)"
     v-for="(item,i) in pageVessionList" :key="i">{{item}}</a>
    <a
    @click="handleClick(current+1)"
    :class="{disable:current===Math.ceil(total/limitNum)}"
    >&gt;&gt;</a>
    <a
    @click="handleClick(Math.ceil(total/limitNum))"
    :class="{disable:current===Math.ceil(total/limitNum)}">&gt;&gt;|</a>
  </div>
</template>

<script>
export default {
  props: {
    current: {
      type: Number,
      default: 1,
    },
    limitNum: {
      type: Number,
      default: 10,
    },
    total: {
      type: Number,
      required: true,
    },
    vessionNum: {
      type: Number,
      default: 10,
    },
  },
  computed: {
    minPage() {
      if (this.current - Math.floor(this.vessionNum / 2) < 1) {
        return 1;
      }
      return Math.ceil(this.current - (this.vessionNum / 2));
    },
    maxPage() {
      if ((this.minPage + this.vessionNum - 1) > Math.ceil(this.total / this.limitNum)) {
        return Math.ceil(this.total / this.limitNum);
      }
      return this.minPage + this.vessionNum - 1;
    },
    pageVessionList() {
      const arr = [];
      for (let i = this.minPage; i < this.maxPage + 1; i += 1) {
        arr.push(i);
      }
      return arr;
    },
  },
  methods: {
    handleClick(newPage) {
      this.$emit('changePage', newPage);
    },
  },
};
</script>

<style lang="less" scoped>
@import url("~@/assets/style/global.less");
.pager-container {
  display: flex;
  justify-content: center;
  margin: 50px 0;
  a {
    margin: 10px;
    &.disable {
      color: @disabled-color;
      cursor: not-allowed;
    }
     &.active{
                color: @text-color;
                cursor:text;
            }
  }
}
</style>
