/* eslint-disable  no-restricted-syntax */
import bus from '@/busEvent';
import debounce from '@/utils/debounce';
import loadurl from '@/assets/avatar.jpg';

let imgs = [];
function setImages() {
  for (const img of imgs) {
    const rect = img.dom.getBoundingClientRect();
    img.dom.src = loadurl;
    if (rect.top > -140 && rect.top < document.documentElement.clientHeight) {
      img.dom.src = img.src;
      imgs = imgs.filter((i) => i !== img);
    }
  }
}
bus.$on('listScroll', debounce(setImages, 100));

export default {
  inserted(el, binding) {
    imgs.push({
      dom: el,
      src: binding.value,
    });
    setImages();
  },
  unbind(el) {
    imgs = imgs.filter((i) => i !== el);
  },
};
