<template>
  <div class="message-container" ref="but">
    <FormMessage
      formType="留言板"
      :subTitle="`${list.total}`"
      :list="list.rows"
      :isListLoading="isLoading"
      @submit="handleSubmit"
    />
  </div>
</template>

<script>
import FormMessage from '@/components/FormMessage/index.vue';
import { getMessage, postMessage } from '@/api/getMessage';
import debounce from '@/utils/debounce';

export default {
  components: {
    FormMessage,
  },
  data() {
    return {
      list: {
        total: 0,
        rows: [],
      },
      isLoading: false,
      current: 1,
      limit: 10,
      page: 1,
    };
  },
  async created() {
    this.isLoading = true;
    this.list = await getMessage();
    this.isLoading = false;
  },
  computed: {
    hasmore() {
      return this.list.total > this.list.rows.length;
    },
  },
  mounted() {
    this.$refs.but.addEventListener('scroll', debounce(this.handleScroll, 50));
  },
  beforeDestroyed() {
    this.$refs.but.removeEventListener(
      'scroll',
      debounce(this.handleScroll, 50),
    );
  },
  methods: {
    async handleSubmit(formData, callback) {
      const res = await postMessage(formData);
      this.list.rows.unshift(res);
      this.list.total += 1;
      callback();
    },
    async fetchMore() {
      if (!this.hasmore) {
        return;
      }
      this.isLoading = true;
      this.page += 1;
      const resp = await getMessage({
        params: {
          page: this.page,
          limit: 10,
        },
      });
      this.list.total = resp.total;
      this.list.rows = this.list.rows.concat(resp.rows);
      this.isLoading = false;
    },
    handleScroll() {
      if (
        Math.abs(
          this.$refs.but.scrollTop
            + this.$refs.but.clientHeight
            - this.$refs.but.scrollHeight,
        ) < 100
      ) {
        this.fetchMore();
      }
    },
  },
};
</script>

<style lang="less" sco[ed]>
.message-container {
  width: 800px;
  height: 700px;
  overflow-y: scroll;
}
</style>
