<template>
  <div class="home-container"
  v-loading='isLoading'
  ref="container" @wheel="scoll">
    <ul class="carousel-container" :style="{marginTop }"
    @transitionend="transitioned"
    >
      <li
      v-for="(item) in data"
      :key="item.id"
      class="image-box">
        <BannerItem :item="item" />
      </li>
    </ul>
    <Icon v-show="index!== 0" class="arrow-up" type="arrowUp"
    @click.native="changeIndex(index -1 )"
     />
    <Icon v-show="index !==  data.length -1" class="arrow-down" type="arrowDown"
    @click.native="changeIndex(index +1 )"
     />
    <ul class="dot-container">
      <li class="dot"
      v-for="(item,i) in  data"
      :key="item.id"
      :class="{active:index === i}"
      @click="changeIndex(i)"
      ></li>
    </ul>
  </div>
</template>

<script>
/* eslint-disable  no-return-await */
import Icon from '@/components/Icon/index.vue';
import getBanners from '@/api/getBanners';
import getData from '@/mixins/getData';
import BannerItem from './BannerItem.vue';

export default {
  mixins: [getData([])],
  components: {
    BannerItem,
    Icon,
  },
  data() {
    return {
      index: 0,
      viewHeight: 0,
      isScoll: false,
    };
  },
  methods: {
    changeIndex(newValue) {
      this.index = newValue;
    },
    scoll(e) {
      if (e.deltaY > 25 && this.index < this.data.length - 1 && !this.isScoll) {
        this.index += 1;
        this.isScoll = true;
        return true;
      } if (e.deltaY < -25 && this.index > 0 && !this.isScoll) {
        this.index -= 1;
        this.isScoll = true;
        return true;
      }
      return false;
    },
    transitioned() {
      this.isScoll = false;
    },
    getViewHeight() {
      this.viewHeight = this.$refs.container.clientHeight;
    },
    async fetchData() {
      return await getBanners();
    },
  },
  computed: {
    marginTop() {
      return `${-this.index * this.viewHeight}px`;
    },
  },
  mounted() {
    this.getViewHeight();
    window.addEventListener('resize', this.getViewHeight);
  },
  destroyed() {
    window.removeEventListener('resize', this.getViewHeight);
  },
};
</script>

<style lang="less" scoped>
@import url("~@/assets/style/var.less");
@import url("~@/assets/style/mixin.less");
.home-container {
  .self-fill(relative);
  background-color: @grey;

  .iconfont {
    position: absolute;
    left: 50%;
    transform: translateX(-50%);
    font-size: 50px;
    color: @text-color-secondary;
    cursor: pointer;
    &.arrow-up {
      top: 25px;
      font-size: 50px;
      animation: jumpUp 3s infinite;
    }
    &.arrow-down {
      bottom: 25px;
      animation: jumpDown 3s infinite;
    }
    @keyframes jumpUp {
      0% {
        transform: translateY(15%);
      }
      50% {
        transform: translateY(-15%);
      }
      100% {
        transform: translateY(15%);
      }
    }
    @keyframes jumpDown {
      0% {
        transform: translateY(-15%);
      }
      50% {
        transform: translateY(15%);
      }
      100% {
        transform: translateY(-15%);
      }
    }
  }
  .dot-container{
    position: absolute;
    right: 20px;
    top: 50%;
    transform: translateY(-50%);
    .dot{
      width: 4px;
      height: 4px;
      background-color: @dark-grey;
      border-radius: 50%;
      margin-bottom: 10px;
      border: 1px solid #fff;
      cursor: pointer;
      &.active{
        background-color: #fff;
      }
    }
  }
  .carousel-container{
    .self-fill();
    transition: .3s;
    .image-box{
      width: 100%;
      position: relative;
      height: 100%;
      overflow: hidden;
    }
  }
}
</style>
