<template>
  <div class="blog-content-container" >
    <div class="blog">
      <h2>{{ datas.title }}</h2>
      <div class="subtitle">
        <span
          >日期:{{
            datas.createDate ? formatDate(+datas.createDate) : ""
          }}</span
        >
        <span>浏览:{{ datas.scanNumber }}</span>
        <a href="#article-md-title-7"><span>评论:{{ datas.commentNumber }}</span></a>
        <span>分类:{{ datas.category ? datas.category.name : "未分类" }}</span>
      </div>
      <div class="content" v-html="datas.htmlContent"></div>
    </div>
    <div class="comment" v-if="!loaded">
      <FormMessage
        formType="评论列表"
        :subTitle="`${data.total}`"
        :list="data.rows"
        :isListLoading="isLoading"
        @submit="handleSubmit"
        ref="comment"
      />
    </div>
  </div>
</template>

<script>
/* eslint-disable  no-return-await */
import FormMessage from '@/components/FormMessage/index.vue';
import formatDate from '@/utils/formatDate';
import 'highlight.js/styles/github.css';
import getData from '@/mixins/getData';
import { getComment, postComment } from '@/api/getBlog';

export default {
  mixins: [getData({ total: 0, rows: [] })],
  props: ['datas', 'loaded'],
  data() {
    return {
      commentOption: {
        page: 1,
        limit: 10,
        blogId: '',
      },
    };
  },
  components: {
    FormMessage,
  },
  computed: {
    hasmore() {
      return this.data.total > this.data.rows.length;
    },
  },
  created() {
    this.$bus.$on('mainscroll', this.handle);
  },
  destroyed() {
    this.$bus.$off('mainscroll', this.handle);
  },
  methods: {
    formatDate,
    async fetchData() {
      return await getComment(this.commentOption);
    },
    async handleSubmit(formData, callback) {
      const res = await postComment({
        blogId: this.$route.params.id,
        ...formData,
      });
      this.data.rows.unshift(res);
      this.data.total += 1;
      callback();
    },
    async fetchMore() {
      if (!this.hasmore) {
        return;
      }
      this.isLoading = true;
      this.page += 1;
      const resp = await this.fetchData();
      this.data.total = resp.total;
      this.data.rows = this.data.rows.concat(resp.rows);
      this.isLoading = false;
    },
    handle(dom) {
      if (this.isLoading || !dom) {
        return;
      }
      const range = 100;
      const dec = Math.abs((dom.scrollTop + dom.clientHeight) - dom.scrollHeight);
      if (dec < range) {
        this.fetchMore();
      }
    },
  },
};
</script>

<style lang="less" scoped>
@import url("~@/assets/style/var.less");
@import url("~@/assets/style/mixin.less");
