<template>
  <div class="form-content-container" ref="container">
    <form class="form" @submit.prevent="handleSubmit">
      <div class="area-user">
        <input
          type="text"
          placeholder="用户昵称"
          v-model="formData.nick"
          maxlength="10"
        />
        <span class="nick">{{ formData.nick.length }}/10</span>
        <div class="err">{{ formData.nickerr }}</div>
      </div>
      <div class="area-content">
        <textarea
          placeholder="输入内容"
          v-model="formData.content"
          rows="10"
          style="resize: none"
        ></textarea>
        <span class="content">{{ formData.content.length }}/300</span>
        <div class="err" >{{formData.contenterr }}</div>
      </div>

      <button type="submit" @submit.prevent="handleSubmit" :disabled="isSubmit">
        {{ isSubmit ? "提交中..." : "提交" }}
      </button>
    </form>
  </div>
</template>

<script>
export default {
  data() {
    return {
      formData: {
        nick: '',
        content: '',
        nickerr: '',
        contenterr: '',
      },
      isSubmit: false,
    };
  },
  methods: {
    handleSubmit() {
      this.formData.nickerr = this.formData.nick ? '' : '昵称不能为空';
      this.formData.contenterr = this.formData.content ? '' : '内容不能为空';
      if (!this.formData.nickerr && !this.formData.contenterr) {
        this.isSubmit = true;
        this.$emit('submit', this.formData, () => {
          this.formData.nick = '';
          this.formData.content = '';
          this.isSubmit = false;
          this.$message({
            content: '评论成功',
            duration: '2000',
            container: this.$refs.container,
            type: 'success',
          });
        });
      }
    },
  },
};
</script>

