<template>
  <div class="toc-list-container">
      <li
      v-for="item in tocWithSelect"
      :key="item.id"
      :class="{selected:item.selected}"
      @click.stop="handle(item.anchor)"
      class="list">
        {{item.name}}
        <TocList :tocList="item.children" @changeToc='handle'/>
      </li>
  </div>
</template>

<script>
/* eslint-disable  no-restricted-syntax */
/* eslint-disable  no-continue */
import deBounce from '@/utils/debounce';

export default {
  name: 'TocList',
  props: ['tocList'],
  data() {
    return {
      currentAnch: '',
    };
  },
  computed: {
    tocWithSelect() {
      const getArr = (toc = []) => toc.map((t) => ({
        ...t,
        selected: t.anchor === this.currentAnch,
        children: getArr(t.children),
      }));
      return getArr(this.tocList);
    },
    doms() {
      const arr = [];
      const getDom = (list) => {
        for (const dom of list) {
          arr.push(document.getElementById(dom.anchor));
          if (dom.children && dom.children.length > 0) {
            getDom(dom.children);
          }
        }
        return arr;
      };
      return getDom(this.tocList);
    },
  },
  methods: {
    handle(item) {
      this.$emit('changeToc', item);
    },
    changeSelected() {
      this.currentAnch = '';
      for (const i of this.doms) {
        if (!i) {
          continue;
        }
        const { top } = i.getBoundingClientRect();
        if (top >= 0 && top <= 150) {
          this.currentAnch = i.id;
          return;
        } if (top > 150) {
          return;
        }
        this.currentAnch = i.id;
      }
    },
  },
  created() {
    this.sele = deBounce(this.changeSelected, 50);
    this.$bus.$on('mainscroll', this.sele);
  },
  destroyed() {
    this.$bus.$off('mainscroll', this.sele);
  },
};
</script>

<style scoped lang='less'>
@import url("~@/assets/style/var.less");
@import url("~@/assets/style/mixin.less");
.toc-list-container {
  .self-fill(relative);
  padding: 10px;
  box-sizing: border-box;
  .list {
    margin: 10px 0;
    color: @heading-color;
    font-weight: initial;
    cursor: pointer;
    font-size: 14px;
    &.selected {
      font-weight: bolder;
      color:@disabled-color;
    }
  }
}
</style>
