<template>
  <div class="blog-list-container" v-loading='isLoading' ref="blogcontainer">
    <li v-for="item in data.rows" :key="item.id" class="blog-item" ref="itemList">
      <Lyout>
        <template #left>
          <router-link :to="{path:'/Blog/' + item.id}">
            <img v-lazy="item.thumb" class="img" alt="" />
          </router-link>

        </template>
        <div class="main">
          <router-link :to="{path:'/Blog/' + item.id}">
            <h2>{{ item.title  }}</h2>
          </router-link>

          <div class="subtitle">
            <span>日期: {{ formatDate(+item.createTime) }}</span>
            <span>浏览: {{ item.scanNumber }}</span>
            <span>评论: {{ item.commentNumber }}</span>
            <span>
              <router-link :to="'/Blog/cate/'+ item.category.id">
                分类: {{ item.category ? item.category.name : "未分类" }}
              </router-link>
            </span
            >
          </div>
          <div class="desc">
            {{ item.desc }}
          </div>
        </div>
      </Lyout>
    </li>
    <Pager
    v-if="data.total"
    :total="+data.total"
    :limitNum ='routeInfo.limit'
     @changePage="changePage" :current='routeInfo.page'/>
  </div>
</template>

<script>
/* eslint-disable  no-return-await */
import { getBlogList } from '@/api/getBlog';
import getData from '@/mixins/getData';
import Lyout from '@/components/Lyout/index.vue';
import Pager from '@/components/Pager/index.vue';
import formatDate from '@/utils/formatDate';

export default {
  mixins: [getData({})],
  components: {
    Lyout,
    Pager,
  },
  methods: {
    getBlogList,
    formatDate,
    async fetchData() {
      return await getBlogList(
        this.routeInfo.page,
        this.routeInfo.limit,
        this.routeInfo.categoryid,
      );
    },
    changePage(newPage) {
      if (newPage < 1) {
        this.current = 1;
      } else if (newPage > Math.ceil(+this.data.total / this.routeInfo.limit)) {
        this.current = Math.ceil(+this.data.total / this.routeInfo.limit);
      } else {
        this.current = newPage;
      }
      if (this.routeInfo.categoryid === -1) {
        this.$router.push({
          name: 'Blog',
          query: {
            page: this.current,
            limit: this.routeInfo.limit,
          },
        });
      } else {
        this.$router.push({
          name: 'BlogCategory',
          query: {
            page: this.current,
            limit: this.routeInfo.limit,
          },
          params: {
            categoryid: this.routeInfo.categoryid,
          },
        });
      }
    },
    handle() {
      this.$bus.$emit('listScroll', this.$refs.itemList);
    },
  },
  mounted() {
    this.$refs.blogcontainer.addEventListener('scroll', this.handle);
  },
  beforedestroy() {
    this.$refs.blogcontainer.removeEventListener('scroll', this.handle);
  },
  watch: {
    async $route() {
      this.isLoading = true;
      this.$refs.blogcontainer.scrollTop = 0;
      this.data = await this.fetchData();
      this.isLoading = false;
    },
  },
  computed: {
    routeInfo() {
      const categoryid = +this.$route.params.categoryid || -1;
      const page = +this.$route.query.page || 1;
      const limit = +this.$route.query.limit || 10;
      return {
        categoryid,
        page,
        limit,
      };
    },
  },
};
</script>

<style lang= 'less' scoped>
@import url("~@/assets/style/var.less");
@import url("~@/assets/style/mixin.less");
.blog-list-container {
  box-sizing: border-box;
  .self-fill(relative);
  overflow-y: scroll;
  padding: 0 20px;
  .blog-item {
    box-sizing: border-box;
    padding: 0 0 10px 0;
    margin: 10px 0;
    border-bottom: 1px solid @light-grey;
    .img {
      border-radius: 7px;
      width:150px;
      height: 140px;
      box-shadow: 1px 1px rgba(0, 0, 0, 0.3), -1px 1px rgba(0, 0, 0, 0.3),
        1px -1px rgba(0, 0, 0, 0.3), -1px -1px rgba(0, 0, 0, 0.3);
    }
    .main {
      padding-left: 10px;
    }
    .subtitle {
      color: @light-grey;
      font-size: 12px;
      margin-bottom: 10px;
      display: flex;
      span {
        margin-right: 10px;
      }
    }
  }
}
</style>
