<template>
  <ul class="list-right-container" v-loading = 'isLoading'>
    <span class="title">文章分类</span>
    <li
      v-for="item in datas"
      :key="item.id"
      class="list-item"
      :class="{ selected: item.select }"
      @click="handle(item)"
    >
      <span class="classify">{{ item.name }}</span>
      <span class="total">{{ item.articleCount }}</span>
    </li>
  </ul>
</template>

<script>
/* eslint-disable  no-return-await */
import { getBlogCategory } from '@/api/getBlog';
import getData from '@/mixins/getData';

export default {
  mixins: [getData([])],
  computed: {
    routeInfo() {
      const categoryid = +this.$route.params.categoryid || -1;
      const page = +this.$route.query.page || 1;
      const limit = +this.$route.query.limit || 10;
      return {
        categoryid,
        page,
        limit,
      };
    },
    datas() {
      const num = this.data.reduce((a, b) => a + b.articleCount, 0);
      const temp = [
        {
          articleCount: num,
          categoryid: -1,
          name: '全部',
        },
        ...this.data,
      ];
      return temp.map((item) => ({
        ...item,
        select: this.routeInfo.categoryid ? +item.id === this.routeInfo.categoryid : false,
      }));
    },
  },
  methods: {
    getBlogCategory,
    async fetchData() {
      return await getBlogCategory();
    },
    handle(item) {
      if (item.id === -1) {
        this.$router.push({
          name: 'Blog',
          query: {
            page: this.routeInfo.page,
            limit: this.routeInfo.limit,
          },
        });
      } else {
        this.$router.push({
          name: 'BlogCategory',
          query: {
            page: this.routeInfo.page,
            limit: this.routeInfo.limit,
          },
          params: {
            categoryid: item.id,
          },
        });
      }
    },
  },
};
</script>

<style lang='less' scoped>
@import url("~@/assets/style/var.less");
@import url("~@/assets/style/mixin.less");
.list-right-container {
  overflow-y: scroll;
  .self-fill(relative);
  padding: 10px;
  .title {
    display: block;
    margin-top: 10px;
    font-weight: bolder;
  }
  .list-item {
    margin: 10px 0;
    color: @heading-color;
    font-weight: initial;
    cursor: pointer;
    font-size: 16px;
    .total {
      color: @grey;
      margin-left: 15px;
      font-size: 10px;
    }
    &.selected {
      font-weight: bolder;
      color: @black;
    }
  }
}
</style>
