<template>
  <div class="blog-item-container" >
    <Lyout>
      <div class="main" ref="contentContainer">
        <BlogContent :datas="data" v-loading="isLoading" :loaded='isLoading'/>
      </div>
      <template #right>
        <div class="right">
          <h2>目录</h2>
          <TocList
            :tocList="data.toc"
            @changeToc="changeToc"
            v-loading="isLoading"
          />
        </div>
      </template>
    </Lyout>
  </div>
</template>

<script>
import BlogContent from '@/views/Blog/components/BlogContent.vue';
import Lyout from '@/components/Lyout/index.vue';
import TocList from '@/views/Blog/components/TocList.vue';
import getData from '@/mixins/getData';
import * as api from '@/api/getBlog';

export default {
  components: {
    Lyout,
    TocList,
    BlogContent,
  },
  mixins: [getData({})],
  methods: {
    async fetchData() {
      const res = await api.getBlog(this.$route.params.id);
      return res;
    },
    changeToc(item) {
      window.location.hash = item;
    },
    handleScroll() {
      this.$bus.$emit('mainscroll', this.$refs.contentContainer);
    },
    scrollInit(top) {
      this.$refs.contentContainer.scrollTop = top;
    },
  },
  updated() {
    const { hash } = window.location;
    window.location.hash = '';
    setTimeout(() => {
      window.location.hash = hash;
    }, 50);
  },
  mounted() {
    this.$refs.contentContainer.addEventListener('scroll', this.handleScroll);
  },
  created() {
    this.$bus.$on('changescroll', this.scrollInit);
  },
  beforeDestroy() {
    this.$refs.contentContainer.removeEventListener('scroll', this.handleScroll);
    this.$bus.$off('changescroll', this.scrollInit);
  },
  computed: {},
};
</script>

<style lang="less" scoped>
@import url("~@/assets/style/var.less");
@import url("~@/assets/style/mixin.less");
.blog-item-container {
  .self-fill(relative);
  .main {
    .self-fill(relative);
    box-sizing: border-box;
    overflow-y: scroll;
    scroll-behavior: smooth;
    overflow-x: hidden;
  }
  .right {
    width: 300px;
    height: 100%;
  }
}
</style>
