<template>
  <div class="image-loader-container">
    <img v-if="!isEverydone" class="placeholder" :src="placeholder" alt="">
    <img @load="handle"
    :style="{opacity:originOpcity,transition:`${duration}ms`}" :src="src" alt="">
  </div>
</template>

<script>
export default {
  props: {
    src: {
      type: String,
      required: true,
    },
    placeholder: {
      type: String,
      required: true,
    },
    duration: {
      type: Number,
      default: 500,
    },
  },
  data() {
    return {
      originIsLoaded: false,
      isEverydone: false,
    };
  },
  computed: {
    originOpcity() {
      return this.originIsLoaded ? 1 : 0;
    },
  },
  methods: {
    handle() {
      this.originIsLoaded = true;
      setTimeout(() => {
        this.isEverydone = true;
        this.$emit('loaded');
      }, this.duration);
    },
  },
};
</script>

<style lang="less" scoped>
@import url(~@/assets/style/mixin.less);
    .image-loader-container{
        width:100%;
        height: 100%;
        border-radius: 5px;
        position: relative;
        overflow: hidden;
        img{
          .self-fill();
          object-fit: cover;
        }
        .placeholder{
          filter: blur(1vw);
        }
    }
</style>
