<template>
  <div class="banner-item-container" ref="bannerBox" @mousemove="moveTo" @mouseleave="moveLeave">
      <div class="carousel-img" ref="banner">
          <ImageLoader :src="item.bigImg" :placeholder="item.midImg" @loaded="loaded"/>
      </div>
    <div class="title" ref="title">{{item.title}}</div>
    <div class="desc" ref="desc">{{item.desc}}</div>
  </div>
</template>

<script>
/* eslint-disable no-param-reassign */
/* eslint-disable no-unused-expressions */

import ImageLoader from '@/components/ImageLoader/index.vue';

export default {
  props: {
    item: {
      type: Object,
      required: true,
    },
  },
  components: {
    ImageLoader,
  },
  computed: {
    widthPro() {
      return (this.bannerSize.width - this.bannerBoxSize.width) / this.bannerBoxSize.width;
    },
    heightPro() {
      return (this.bannerSize.height - this.bannerBoxSize.height) / this.bannerBoxSize.height;
    },
  },
  data() {
    return {
      titleWidth: 0,
      descWidth: 0,
      bannerSize: {
        width: 0,
        height: 0,
      },
      bannerBoxSize: {
        width: 0,
        height: 0,
      },
    };
  },
  mounted() {
    this.titleWidth = this.$refs.title.clientWidth;
    this.descWidth = this.$refs.desc.clientWidth;
    this.getBannerSize();
    this.getBannerBoxSize();
    this.moveLeave();
  },
  methods: {
    loaded() {
      this.textAnima(this.$refs.title, this.titleWidth, '1s');
      this.textAnima(this.$refs.desc, this.descWidth, '2s');
    },
    textAnima(ele, eleWidth, time) {
      ele.style.width = 0;
      ele.style.opacity = 1;
      ele.clientWidth;
      ele.style.transition = time;
      ele.style.width = `${eleWidth}px`;
    },
    getBannerSize() {
      this.bannerSize = {
        width: this.$refs.banner.clientWidth,
        height: this.$refs.banner.clientHeight,
      };
    },
    getBannerBoxSize() {
      this.bannerBoxSize = {
        width: this.$refs.bannerBox.clientWidth,
        height: this.$refs.bannerBox.clientHeight,
      };
    },
    moveTo(e) {
      this.$refs.banner.style.top = `${-(e.clientY - this.$refs.bannerBox.getBoundingClientRect().top) * this.heightPro}px`;
      this.$refs.banner.style.left = `${-(e.clientX - this.$refs.bannerBox.getBoundingClientRect().left) * this.widthPro}px`;
    },
    moveLeave() {
      this.$refs.banner.style.top = `${(-this.bannerBoxSize.height / 2) * this.heightPro}px`;
      this.$refs.banner.style.left = `${(-this.bannerBoxSize.width / 2) * this.widthPro}px`;
    },
  },
};
</script>

<style lang='less' scoped>
@import url("~@/assets/style/var.less");
@import url("~@/assets/style/mixin.less");
.banner-item-container{
    width: 100%;
    height: 100%;
    position: relative;
    .carousel-img{
    width: 110%;
    height: 110%;
    position: absolute;
    top: 0;
    left: 0;
    transition: .3s;
    }
    .title,.desc{
        position: absolute;
        left: 30%;
        color: #fff;
        text-shadow: 1px 0 0 rgba(0,0,0,0.5),0 1px 0 rgba(0,0,0,0.5),
        -1px 0 0 rgba(0,0,0,0.5),0 -1px 0 rgba(0,0,0,0.5);
        opacity: 0;
        white-space: nowrap;
        overflow: hidden;
    }
    .title{
        top: 70%;
        font-size: 30px;
    }
    .desc{
        top: 80%;
        font-size: 25px;
    }
}
</style>
